import React, {useState} from 'react'
import {Provider} from 'react-redux'

import Game from './Game'
import HomePage from './HomePage'
import {isMobile} from 'react-device-detect'
import Plug from './Plug'
import Hint1 from '../images/hint/hint1.svg'
import Hint2 from '../images/hint/hint2.svg'
import Hint3 from '../images/hint/hint3.svg'
import Hint4 from '../images/hint/hint4.svg'
import Hint5 from '../images/hint/hint5.svg'
import Hint6 from '../images/hint/hint6.svg'
import Hint7 from '../images/hint/hint7.svg'
import Hint8 from '../images/hint/hint8.svg'
import Hint9 from '../images/hint/hint9.svg'
import Hint10 from '../images/hint/hint10.svg'
import Hint11 from '../images/hint/hint11.svg'

const App = ({store}) => {
  const [openGame, setOpenGame] = useState(false)
  const mapHint = [Hint1, Hint2, Hint3, Hint4, Hint5, Hint6, Hint7, Hint7, Hint8, Hint9, Hint10, Hint11]
  function randomMap(arr){
    return arr.map(i=>[Math.random(), i]).sort().map(i=>i[1])
  }
  console.log(22222)
  return (
    <Provider store={store}>
      {isMobile ?
        <>
          {openGame ?
            <Game mapHint={randomMap(mapHint)}/> :
            <HomePage setOpenGame={setOpenGame}
            />}
        </>
        :
        <Plug/>
      }
    </Provider>
  )
}

export default App
