const initialState = {
  x: 400,
  pipes: [],
}

export default (state = initialState, {type} = {}) => {
  switch (type) {
    case 'RUNNING':
      if (!state.pipes.length) {
        return state
      }

      return {...state, x: state.x - 20}
    case 'GENERATE':
      const topHeight = Math.round(Math.random() * 280) + 40
      const widthCloud = Math.floor(Math.random() * 100)
      const widthCloudTop = Math.floor(Math.random() * 100) + 20
      return {...state, pipes: [...state.pipes, {topHeight}, {widthCloud}, {widthCloudTop}]}
    case 'GAME_OVER':
      return initialState
    default:
      return state
  }
}
