import React, {useCallback, useEffect, useRef, useState} from 'react'
import {connect} from 'react-redux'

import TopPipeImage from '../images/pipe-top.svg'
import TopPipeImage2 from '../images/levelTho/TopPipeImage2.svg'
import BottomPipeImage from '../images/pipe-bottom.svg'
import BottomPipeImage2 from '../images/levelTho/BottomPipeImage.svg'
import {useInView} from 'react-intersection-observer'
import Cloud from '../images/cloud/home/3.svg'
import Star from '../images/levelTho/Star.svg'
import Fire from '../images/superLevel/Fire.svg'
import Lightning from '../images/superLevel/Lightning.svg'




const Pipe = ({x, pipes, setScopeTime, superLevel, levelTho, mapHint, countHint, setCountHint}) => {
  const [indexMapHint,setIndexMapHint] =  useState(0)
  const windowSize = window.screen.height
  const [count, setCount] = useState(0)


  useEffect(()=>{
    if(document.getElementsByClassName('listPipe')[count]) {
      if (document.getElementsByClassName('listPipe')[count].getBoundingClientRect().left < 200) {
        setCount(count => count + 1)
        setScopeTime(count)
      }
    }
  })

  const imgMini = (i)=> {
    if (superLevel){
      if( i % 7 === 0){
        return Fire
      }else if( i % 5 === 0){
        return Lightning
      } else if (i % 4 === 0 ){
        return Star
      }
    } else if (levelTho){
      return Star
    } else {
      return Cloud
    }
  }


  const imgHint = (i) => {
    if (i % 4 === 0)
      return mapHint[countHint]
  }

  useEffect(()=>{
    if (count % 4 === 3) {
      if (mapHint.length > countHint) {
        setCountHint(countHint => countHint + 1)
      } else {
        setCountHint(0)
      }
    }
  },[count])

  return (
    <div
      style={{
        position: 'relative',
      }}>

      {pipes.map(({topHeight, widthCloud, widthCloudTop}, i) => (

        <div
          key={`pipe-${i}`}
          style={{
            position: 'relative',
          }}>
          <div
            style={{
              backgroundSize:'cover',
              position: 'absolute',
              top: 0,
              left: x + i * 100,
              width: 107,
              height: topHeight,
              zIndex:300,
              background: `url(${superLevel? TopPipeImage2 :TopPipeImage})`,
              backgroundPosition: 'bottom',
              transition: 'left 300ms',
            }}></div>
          <div className={'listPipe'} style={{
            position:'absolute',
            top: topHeight,
            height:200,
            width: 107,
            left: x + i * 300,
          }} />
          <img style={{
            position:'absolute',
            top: widthCloudTop,
            height:'auto',
            width: widthCloud,
            zIndex:200,
            left:  (x * 0.2) + i * 100 + 320,
            transition:'left 0.3s,',
          }} src={imgMini(i)}/>

          <img
            style={{
            position:'absolute',
            top: topHeight + 100,
            height:'auto',
            zIndex:320,
            bottom:0,
            left: x + i * 100,
            transition: 'left 300ms',
          }} src={imgHint(i)}/>
          <div
            style={{
              // backgroundSize:'cover',
              backgroundRepeat:'no-repeat',
              position: 'absolute',
              top: topHeight + 200,
              left: x + i * 100,
              bottom: 0,
              // backgroundSize: '100% 100%',
              width: 107,
              zIndex:300,
              height: `calc(${windowSize}px - ${topHeight}px)`,
              background: `url(${superLevel? BottomPipeImage2 :BottomPipeImage})`,
              transition: 'left 300ms',
            }}></div>
        </div>
      ))}
    </div>
  )
}

const mapStateToProps = ({pipe}) => ({x: pipe.x, pipes: pipe.pipes, widthCloud:pipe.widthCloud, widthCloudTop:pipe.widthCloudTop})
const mapDispatchToProps = {}

export default connect(mapStateToProps, mapDispatchToProps)(Pipe)
