import React, {useEffect, useRef, useState} from 'react'
import {connect} from 'react-redux'

import Bird from './Bird'
import Pipe from './Pipe'
import Foreground from './Foreground'
import Score from './Score'
import GameOver from './GameOver'
import Hint1 from '../images/hint/hint1.svg'
import Hint2 from '../images/hint/hint2.svg'
import Hint3 from '../images/hint/hint3.svg'
import Hint4 from '../images/hint/hint4.svg'
import Hint5 from '../images/hint/hint5.svg'
import Hint6 from '../images/hint/hint6.svg'
import Hint7 from '../images/hint/hint7.svg'
import Hint8 from '../images/hint/hint8.svg'
import Hint9 from '../images/hint/hint9.svg'
import Hint10 from '../images/hint/hint10.svg'
import Hint11 from '../images/hint/hint11.svg'

let gameLoop
let pipeGenerator
const Game = ({status, start, fly, mapHint}) => {
  const [statusGame, setStatusGame] = useState(true)
  const ref =useRef(null)
  const [countHint, setCountHint] = useState(0)
  const [scopeTime, setScopeTime] = useState(0)
  const [gameOverCount, setGameOverCount] = useState(0)
  const [levelTho, setLevelTho] = useState(false)
  const [superLevel, setSuperLevel] = useState(false)
  const [windowHeight, setWindowHeight] = useState(0)

  if (status === 'game-over') {
    clearInterval(gameLoop)
    clearInterval(pipeGenerator)
  }


  useEffect(()=>{
      if (scopeTime % 7 === 0) {
        setLevelTho(levelTho=> !levelTho)
    }
  },[scopeTime])

  useEffect(()=>{
    if (status === 'game-over') {
      setGameOverCount(gameOverCount => gameOverCount + 1)
      if(gameOverCount > 10){
        setSuperLevel(true)
      }
    } else {
      setLevelTho(false)
    }
  },[status])

  useEffect(()=>{
    if(superLevel){
      document.body.classList.add('superLevel')
    } else {
      if (!levelTho) {
        document.body.classList.remove('levelTho')
      } else {
        document.body.classList.add('levelTho')
      }
    }
  },[levelTho, superLevel])


  useEffect(() => {
    if(ref && ref.current) {
      const handleKeyPress = () => {
        fly()
        if (status !== 'playing') {
          start()
        }
      }

      ref.current.addEventListener('click', handleKeyPress)
    }
  }, [statusGame])

  useEffect(()=>{
    if (status === 'game-over'){
      setStatusGame(false)
    } else {
      setStatusGame(true)
      setScopeTime(0)
    }
    },[status])

  useEffect(()=>{
    const window = document.documentElement.scrollHeight
    setWindowHeight(window)
  },[])

  useEffect(()=>{
    if (status === 'playing') {
      document.body.classList.add('hidden')
    } else {
      document.body.classList.remove('hidden')
    }
  },[status])


  return (
    <>
      {!statusGame?
        <GameOver superLevel={superLevel} levelTho={levelTho} setGameOverCount={setGameOverCount} gameOverCount={gameOverCount} scopeTime={scopeTime} setStatusGame={setStatusGame}/>
        :
        <div
          ref={ref}
          style={{
            height: `${ windowHeight !== 0? windowHeight : ''}px`,
            position: 'relative',
            width: '100%',
            // height: `${windowHeight}`,
            overflow: 'hidden',
          }}>
          <Score scopeTime={scopeTime} />
          <Bird />
          <Pipe mapHint={mapHint} superLevel={superLevel} levelTho={levelTho} scopeTime={scopeTime} setScopeTime={setScopeTime} countHint={countHint} setCountHint={setCountHint}/>
          <Foreground status={status} superLevel={superLevel} />
        </div>
      }
    </>
  )
}

const fly = () => {
  return (dispatch) => {
    dispatch({type: 'FLY'})
  }
}

const start = () => {
  return (dispatch, getState) => {
    const {status} = getState().game

    if (status !== 'playing') {
      gameLoop = setInterval(() => {
        dispatch({type: 'FALL'})
        dispatch({type: 'RUNNING'})

        check(dispatch, getState)
      }, 200)

      pipeGenerator = setInterval(() => {
        dispatch({type: 'GENERATE'})
      }, 2000)

      dispatch({type: 'START'})
    }
  }
}

const check = (dispatch, getState) => {
  const state = getState()
  const birdY = state.bird.y
  const pipes = state.pipe.pipes
  const x = state.pipe.x
  const challenge = pipes
    .map(({topHeight}, i) => {
      return {
        x1: x + i * 100,
        y1: topHeight,
        x2: x + i * 100,
        y2: topHeight + 200,
      }
    })
    .filter(({x1}) => {
      if (x1 > 0 && x1 < 288) {
        return true
      }

    })
  if (birdY < 0 ) {
    dispatch({type: 'GAME_OVER'})
  }
  if (birdY > document.documentElement.scrollHeight - 180) {
    dispatch({type: 'GAME_OVER'})
  }
  if (challenge.length) {
    const {x1, y1, x2, y2} = challenge[0]
    if (
      (x1 < 171 && 171 < x1 + 107 && birdY + 100 < y1) ||
      (x2 < 171 && 171 < x2 + 107 && birdY + 100 > y2)
    ) {
      dispatch({type: 'GAME_OVER'})
    }
  }
}

const mapStateToProps = ({game}) => ({status: game.status})
const mapDispatchToProps = {start, fly}

export default connect(mapStateToProps, mapDispatchToProps)(Game)
