import React from 'react'
import {connect} from 'react-redux'

import BirdImage from '../images/birdImg1.svg'
import BirdImage2 from '../images/birds2.svg'

const Bird = ({y, r}) => {
  return (
    <div
      id={'bird'}
      style={{
        position: 'absolute',
        top: y,
        width: 181,
        height: 173,
        zIndex:1000,
        backgroundSize:'cover',
        backgroundImage: r ===0 ? `url(${BirdImage})`: `url(${BirdImage2})`,
        backgroundRepeat:'no-repeat',
        // transform: `rotate(${r}deg)`,
        transition: 'transform 100ms, top 300ms',
      }}>
    </div>
  )
}

const mapStateToProps = ({bird}) => ({y: bird.y, r: bird.r})
const mapDispatchToProps = {}

export default connect(mapStateToProps, mapDispatchToProps)(Bird)
