import React from 'react'
import FgImage from '../images/earth.svg'
import SuperFgiImage from '../images/superLevel/earthSuper.svg'
const Foreground = ({superLevel, status}) => {
  return (
    <>
      {superLevel?
      <div
        className={`${status === 'playing'? 'start':''}`}
        style={{
          position: 'absolute',
          top: 0,
          width: '100%',
          height: 108,
          zIndex:100,
          background: `url(${superLevel? SuperFgiImage:FgImage})`
        }}/>
      : <></>}
    <div
      className={`foreground ${status === 'playing'? 'start':''}`}
      style={{
        backgroundImage: `url(${superLevel? SuperFgiImage:FgImage})`
      }}/>
      </>
  )
}

export default Foreground
